import { injectGlobal } from 'styled-components';

import theme from './theme';

injectGlobal`
  @import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
  html, body {
    width: 100%;
    height: 100%;
  }

  body {
    font-family: ${theme.font};
    background: ${theme.colors.lightBlue};
  }

  #___gatsby {
    height: 100%;
  }
`;
